











/*css rest*/
html, body, p, h1, h2, h3, h4, h5, h6, div, ul, dl, dd, dt, li, i, b, input,textarea {
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-style: normal;
}
s, i, em {
  font-style: normal;
  text-decoration: none;
}
img {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #999;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;  /*IE/7/6*/
}
html{
  width: 100%;
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  font-family: 'microsoft yahei';
  /*line-height: 1.5;*/
  font-size: 0.16rem;
  color: #000;
}




